.main-navigation {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 3.5rem;
    background: rgb(81, 144, 204);
    padding: 0 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

 
.main-navigation__logo h1 {
    margin: 0;
    font-size: 1.5rem;
}

.main-navigation__items {
    margin-left: 1.5rem;
}

.main-navigation__items ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    list-style: none;
}

.main-navigation__items li {
    margin: 0 1rem;
}

.main-navigation__items a,
.main-navigation__items button {
    text-decoration: none;
    color: black;
    padding: 0.25rem 0.5rem;
    border: none;
    font: inherit;
    background: transparent;
    cursor: pointer;
    vertical-align: middle;
    margin: 0;
}

.main-navigation__items a:hover,
.main-navigation__items a:active,
.main-navigation__items a.active,
.main-navigation__items button:hover,
.main-navigation__items button:active {
    padding: 0.5rem;
    background: #a14c4c;
    color:yellow;
    border-radius: 0.5rem;
}